import React, { Component } from 'react';
import { compose, graphql } from 'react-apollo';
// import gql from 'graphql-tag';
import AdminView from '../components/AdminView';
import Loader from '../../common/components/global/components/Loader';
import '../styles/productspecs.scss';
import ProductSpecs from '../components/ProductSpecs';
// import { ADMIN_PRODUCTS_QUERY /*MODIFY_PRODUCT_DATA*/ } from '../../common/schema';
import PRODUCTS_QUERY from '../graphql/Products.graphql';

const columns = [
  {
    title: '',
    dataIndex: 'node',
    key: 'featuredImage',
    // eslint-disable-next-line
    render: (image, record) => {
      // console.log('TEXT::', text);
      // console.log('RECORD:', record.node);
      return (
        <div>
          {/* {text} */}
          <img src={image.originalSrc} alt="" />
          {/* <Link to={{ pathname: `/admin/locations/${record.node.handle}` }}>{text}</Link> */}
        </div>
      );
    }
  },
  {
    title: (
      <a href="#">
        {/* {t('users.column.name')} {this.renderOrderByArrow('username')} */}
        <h3>Product</h3>
      </a>
    ),
    dataIndex: 'node',
    key: 'title',
    // eslint-disable-next-line
    render: (text, record) => {
      // console.log('TEXT::', text);
      // console.log('RECORD:', record);
      return (
        <div>
          {text}
          {/* <Link to={{ pathname: `/admin/locations/${record.node.handle}` }}>{text}</Link> */}
        </div>
      );
    }
  }
];
const LoadMore = ({ loadMore }) => {
  return (
    <div className="admin__loadmore">
      <button onClick={() => loadMore()}>Load More</button>
    </div>
  );
};
class Products extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    console.log('The PRODUCT PROPS::::', this.props);
    const { productsLoading, sfy_shop} = this.props;

    if (productsLoading) {
      return (
        <AdminView {...this.props}>
          <Loader />
        </AdminView>
      );
    }
    return (
      <AdminView {...this.props}>
        <h2>Product Specifications</h2>
        <div className="admin__tableview">
          <div className="admin__productspecs">
            <ProductSpecs dataSource={sfy_shop.edges} columns={columns} />
          </div>
          <LoadMore loadMore={() => null} />
        </div>
      </AdminView>
    );
  }
}
const ProductsApollo = compose(
  graphql(PRODUCTS_QUERY, {
    options: () => ({
      variables: { first: 50 }
    }),
    props: ({ data }) => {
      console.log('DATA;::', data);
      const { loading, error, admin_products } = data;
      if (error) {
        throw new Error(error);
      }
      if (loading) {
        return { productsLoading: loading };
      }
      if (!loading && admin_products) {
        let currEndCursor, hasNextPage, hasPreviousPage;
        // const { edges } = admin_products;
        // //console.log('products', products);
        // if (edges.length > 0) currEndCursor = edges[edges.length - 1].cursor;
        // hasNextPage = admin_products.pageInfo.hasNextPage;
        // hasPreviousPage = admin_products.pageInfo.hasPreviousPage;

        return { productsLoading: loading, sfy_shop: admin_products };
      }
    }
  })
)(Products);

export default ProductsApollo;
